.LimitTermometer {
  width: auto;
  height: 100%;
  background: #33404c;
  display: grid;
  align-items: flex-end;
  position: relative;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

.OrderLimit {
  position: absolute;
  bottom: 0;
  width: 5px;
  height: 100px;
  background: linear-gradient(
    to top,
    #38ef7d,
    #11998e,
    #11998e,
    #f4c536,
    #ff1100
  ) !important;
}

.OLlimit {
  overflow: hidden;
  transition: 0.2s;
  height: 100%;
  height: 50%;
  position: relative;
  width: 4px;
}

.flag p {
  margin: 0 !important;
  color: #ff4739 !important;
}

.flag {
  position: absolute;
  top: 2px;
  height: 20px;
  color: #38ef7d;
  right: 2px;
  width: 30px;
  font-size: 13px;
}

.contFlag {
  position: relative;
  display: grid;
  z-index: 2;
}

.contFlag .iconSty {
  position: absolute;
  align-self: center;
  justify-self: center;
}

.contFlag > p {
  align-self: center;
  text-align: center;
  align-self: center;
  font-size: 12px;
  color: white !important;
  justify-self: center;
  position: relative;
  z-index: 1;
  margin-bottom: 8px !important;
  font-family: fa5-proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

